export default{ 
	props:{
		controls:{
			type:Boolean,
			default:false
		},
		src:{
			type:String,
			default:''
		},
		short:{
			type:Boolean,
			default:false
		},
		play:{
			type:Boolean,
			default:false
		},
		height:{
			type:String,
			default:''
		},
		width:{
			type:String,
			default:''
		},
		duration:{
			type:Number,
			default:0
		},
		initialTime:{
			type:Number,
			default:0
		}
	},
	data(){
		return{
			time:0
		}
	},
	methods:{
		timeupdate(event){
			
			if(!this.play) return
			if(this.time>=this.duration) this.time=0
			this.time = this.time + 0.25 
			if(this.duration == 0) this.time = 0
		},
		clickVideo(){
			this.$emit('click')
		},
		videoPlay(){
			if(this.play){
				this.videoCtx = uni.createVideoContext(`video_${this.src}`,this);
				this.videoCtx.play();
			}else{
				this.videoCtx = uni.createVideoContext(`video_${this.src}`,this);
				this.videoCtx.pause();
				this.$emit('pause',this.time)
			}
			
		},
		waiting(){
			
		}
	},
	watch:{
		play(newVal,oldVal){
			this.videoPlay()
		},
		startTime:{
			immediate: true,
			handler(newVal,oldVal){
				this.time = newVal
				if(this.duration == 0) this.time = 0
			}
		}
	},
	computed:{
		barWidth(){
			let width = this.time/this.duration*parseInt(this.width)
			
			return `${width}px`
		},
		startTime(){
			return this.initialTime
		}
	}
}
